.hero__section {
    margin-top: 5rem;
    overflow: hidden;
}

.hero__content {
    display: flex;
    align-items: center;
}

.hero__text-side {
    order: 1;
}

.hero__photo {
    order: 2;
    width: 120%;
    margin-right: -250px;
}

.hero__header {
    font-size: 4rem;
   line-height: 4.5rem;
    color: var(--neutral-very-dark-violet);
}

.hero__paragraph {
    color: var(--neutral-grayish-violet);
    margin-bottom: 1.5rem;
    font-size: var(--body-copy-font-size);
    width: 85%;
}

.hero__photo img {
   width: 100%;
}

@media screen and (max-width: 1080px) {
    .hero__header {
      font-size: 2.8rem;
      line-height: 3.5rem;
    }

    .hero__photo {
        margin-right: -150px;
    }

    .hero__section {
        overflow: hidden;
    }
    
}

@media screen and (max-width: 768px) {
    .hero__section {
      margin-top: 1rem;
    }
    .hero__text-side {
        margin-top: 1.5rem;
        order: 2;
        text-align: center;
    }

    .hero__header {
        font-size: 3.4rem;
        margin-bottom: 1rem;
    }
    
    .hero__photo {
        order: 1;
    }

    .hero__content {
        flex-direction: column;
    }

     .hero__paragraph {
        width: 100%;
     }

}

@media screen and (max-width: 450px) {
    .hero__header {
        font-size: 2rem;
        line-height: 2.2rem;
    }

    .hero__paragraph {
        font-size: 14px;
    }
}


