.boost__section {
    background-color: var(--primary-dark-violet);
    background-image: url("../../assets/bg-boost-desktop.svg");
    background-size: cover;
    background-position: center;
}

.boost__content {
    text-align: center;
    padding: 3rem 0;
}

.boost__title {
    color: white;
    margin-bottom: 1rem;
}

.boost__button:hover {
   opacity: 1;
   background-color: rgb(138, 255, 255);
}

@media screen and (max-width: 450px) {
    .boost__title {
        font-size: 1.5rem;
        margin-bottom: 1.5rem;
    }

    .boost__section {
        background-image: url("../../assets/bg-boost-mobile.svg");
    }
}