.footer__section {
    background-color: var(--neutral-very-dark-violet);
}

.footer__content {
    display: flex;
    justify-content: space-between;
    padding: 4.5rem 0;
}

.list__container {
    display: flex;
    gap: 5rem;
}

.list__title {
    margin-bottom: 1.5rem;
    color: white;
}

.list__item + .list__item {
    margin-top: 0.5rem;
}

.list__item a {
    color: var(--neutral-gray);
}

.list__item a:hover, .socials__group a:hover {
    color: var(--primary-cyan);
}

.socials__group {
    display: flex;
    gap: 1.5rem;
    font-size: 1.8rem;
    margin-top: -7px;
}

.socials__group a {
    color: white;
}

@media screen and (max-width: 1080px) {
    .footer__content {
        flex-direction: column;
        padding: 3.5rem 0;
    }

    .footer__logo {
        margin-bottom: 2rem;
    }
}

@media screen and (max-width: 768px) {
    .footer__content {
        text-align: center;
    }

  .list__container {
    flex-direction: column;
    gap: 1.8rem;
    margin-bottom: 2rem;
  }

  .socials__group {
    margin: 0 auto;
  }
}

@media screen and (max-width: 450px) {
    .footer__content {
        padding: 2.5rem 0;
    }

    .socials__group {
        font-size: 1.4rem;
      }
}
