.card {
  background-color: white;
  border-radius: 0.5rem;
  padding: 3rem 1.5rem;
  margin-bottom: 20px;
  position: relative;
  z-index: 2;
}

.card:nth-child(3) {
  top: 40px; 
}

.card:nth-child(4) {
  top: 80px; 
}

.icon {
  position: relative;
  top: -45px;
  left: 0;
  display: inline-block;
  background-color: var(--primary-dark-violet);
  padding: 1.3rem 1.6rem 1rem;
  border-radius: 100%;
  margin-top: -100px;
}

.card__title {
  margin-bottom: 1rem;
  color: var(--neutral-very-dark-violet);
  font-size: 1.3rem;
}

.card__paragraph {
  color: var(--neutral-grayish-violet);
  line-height: 1.5rem;
}


@media screen and (max-width: 1080px) {
  .card {
    text-align: center;
    width: 100%;
  }

  .card:nth-child(3) {
    top: 0px;
    margin-top: 3rem; 
  }
  
  .card:nth-child(4) {
    top: 0px;
    margin-top: 3.2rem;  
  }

}
