.url-shortener {
  margin-top: 8rem;
  position: relative;
  top: -85px;
}

.input__container {
  padding: 3.5rem 3rem;
  border-radius: 0.5rem;
  background-color: var(--primary-dark-violet);
  background-image: url("../../assets/bg-shorten-desktop.svg");
  background-repeat: round;
  margin-bottom: 2rem;
}
  
  .input-section {
    display: flex;
    gap: 1rem;
    position: relative;
  }
  
  .input-section__input {
    flex: 1;
    margin-right: 10px;
    padding: 1rem;
    border-radius: 0.5rem;
    font-size: var(--body-copy-font-size);
    outline: none;
    border: none;
  }
 
  .shorten__button {
    padding: 1rem 2.5rem;
    border-radius: 0.5rem;
    background-color: var(--primary-cyan);
    color: white;
    font-weight: var(--font-weight-700);
    font-size: var(--body-copy-font-size);
    transition: 0.3s;
  }

  .shorten__button:hover {
    background-color: rgb(138, 255, 255);
  }
  
  .error-message {
    color: var(--secondary-red);
    position: absolute;
    top: 70px;
    font-style: italic;
    font-size: 14px;
  }

  .error {
    border: 2px solid var(--secondary-red);
  }

  .error::placeholder {
    color: var(--secondary-red);
    opacity: 0.6;
  }
  
  .history-entry {
    font-weight: var(--font-weight-700);
    margin-bottom: 15px;
    background-color: white;
    box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.1);
    border-radius: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .results__container {
    color: var(--neutral-very-dark-blue);
    width: 50%;
    padding: 1rem 2rem;
  }

  .shortenedUrl__place {
    color: var(--primary-cyan);
    padding: 1rem 2rem;
  }


  @media screen and (max-width: 1080px) {
    .history-entry {
      flex-direction: column;
      text-align: center;
    }
    .copy__button {
      margin: 0;
      margin-top: 0.7rem;
    }

    .results__container {
      width: 100%;
    }

    .shortenedUrl__place {
      display: flex;
      flex-direction: column;
      padding: 0.7rem 2rem;
    }

    .results__mobile-line{
        width: 100%;
        opacity: 0.3;
        border-width: 2px;
    }
  }

  @media screen and (max-width: 768px) {
    .input-section {
      flex-direction: column;
      gap: 3rem;
    }

    .input__container {
      padding: 2rem 1.5rem;
      background-image: url("../../assets/bg-shorten-mobile.svg");
      background-repeat: no-repeat;
      background-position: top right;
  }

  .url-shortener {
    top: -95px;
  }

  .history-entry {
    text-align: left;
    overflow:hidden;
  }

  .shortenedUrl__place {
    width: 100%;
    padding: 0.7rem 1rem;
  }

  .results__container {
    padding: 0.7rem 1rem;
  }

  .input-section__input {
    margin-right: 0;
    padding: 0.8rem;
  }

  .shorten__button {
    padding: 0.8rem 2.5rem;
  }

  .error-message {
    top: 65px;

  }

  @media screen and (max-width: 768px) {
    .input-section {
      gap: 2rem;
    }
  }


  

  

  

  
}