@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap');

:root {
    /* Colors */
    --primary-cyan: hsl(180, 66%, 49%);
    --primary-dark-violet: hsl(257, 27%, 26%);

    --secondary-red: hsl(0, 87%, 67%);

    --neutral-gray: hsl(0, 0%, 75%);
    --neutral-grayish-violet: hsl(257, 7%, 63%);
    --neutral-very-dark-blue: hsl(255, 11%, 22%);
    --neutral-very-dark-violet: hsl(260, 8%, 14%);

    /* Typography */
    --body-copy-font-size: 18px;

    --font-family: 'Poppins', sans-serif;
    --font-weight-500: 500;
    --font-weight-700: 700;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    font-weight: var(--font-weight-700);
}

body,
button,
input,
textarea {
    font-family: var(--font-family);
}

ul{
    list-style: none;
  }
  
  a{
    text-decoration: none;
  }
  
  button{
    cursor: pointer;
    border: none;
    outline: none;
  }

.container {
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
}

.get__started-button {
    padding: 8px 28px;
    font-weight: var(--font-weight-700);
    border-radius: 1.5rem;
}

.get__started-button, .copy__button {
    background-color: var(--primary-cyan);
    color: #ffffff;
}

.copy__button {
    padding: 8px 30px;
    font-weight: var(--font-weight-500);
    border-radius: 0.5rem;
    margin-left: 2rem;
}

.copy__button.copied {
    background-color: var(--primary-dark-violet);
}

.copy__button.copied:hover {
    opacity: 1;
}

.body__button {
    padding: 10px 35px;
}

.get__started-button:hover, .copy__button:hover {
    opacity: 0.6;
    transition: .3s;
}