.header {
  margin-top: 2rem;
}
.nav {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.nav__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.nav__toggle {
  position: absolute;
  right: 2rem;
}

.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
}

.nav__list {
  display: flex;
  column-gap: 1.5rem;
  margin-left: 1rem;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--neutral-grayish-violet);
  transition: .3s;
}

.nav__close,
.nav__toggle {
  display: none;
}

.nav__toggle i, .nav__close i {
  font-size: 1.5rem;
  color: var(--neutral-grayish-violet);
}

.auth__buttons {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.login__button {
  color: var(--neutral-grayish-violet);
  transition: 0.3s;
}

.login__button:hover, .nav__link:hover {
  color: var(--neutral-very-dark-violet);
}

/*  -------------BREAKPOINTS------------- */

@media screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: -100%;
    width: 80%;
    background-color: var(--primary-dark-violet);
    padding: 2.5rem 2rem;
    border-radius: 0.7rem;
    transition: 0.3s;
    z-index: 1;
  }

  /* Showing menu */

  .show-menu {
    top: 10%;
  }

  .nav__icon {
    font-size: 1.2rem;
  }

  .nav__close {
    position: absolute;
    right: 1.3rem;
    top: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }

  .nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav__list {
    display: flex;
    flex-direction: column;
    gap: 1.7rem;
    margin: 0 auto;
    width: 100%;
    padding-bottom: 2rem;
    border-bottom: 1.5px solid rgba(255, 255, 255, .1);
  
  }

  .nav__link, .login__button {
    color: white;
  }

  .nav__close,
  .nav__toggle {
    display: block;
  }

  .nav__menu {
    flex-direction: column;
  }

  .auth__buttons {
    flex-direction: column;
    gap: 1.3rem;
    margin-top: 2rem;
    width: 100%;
  }

  .nav__signup-button {
    width: 100%;
  }
}
