.card__section {
    background-color: hsl(0, 0%, 95%);
    padding-bottom: 10rem;
}

.cards__container {
    display: flex;
    gap: 1.7rem;
    align-items: center;
}

.subheader__content {
    text-align: center;
    margin-bottom: 5rem;
}

.statistics__subheader {
    color: var(--neutral-very-dark-violet);
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
}

.statistics__paragraph {
    color: var(--neutral-grayish-violet);
}

.connector__line {
    border: 4px solid var(--primary-cyan);
    position: absolute;
    z-index: 1;
    width: 60%;
}


@media screen and (max-width: 1080px) {
   .cards__container {
    flex-direction: column;
   }

   .card__section {
    background-color: hsl(0, 0%, 95%);
    padding-bottom: 3rem;
}

.connector__line {
    width: auto;
    height: 880px;
}
}

@media screen and (max-width: 768px) {
    .statistics__subheader {
        font-size: 2rem;
    }
    .statistics__paragraph br{
        display: none;
    }
}

@media screen and (max-width: 450px) {
    .connector__line {
        height: 1025px;
    }
}


   
